.ContactUs {
  background: #e6e6e6;
  padding: 2rem;
}

.ContactUs h2 {
  text-align: center;
  margin-bottom: 2rem;
}

.ContactUsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ContactUsInputs {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: 1rem 0;
}

.ContactUsInputs input,
.ContactUsInputs textarea {
  padding: 1rem;
  border: 1px solid #ddd;
  margin-bottom: 1rem;
  width: 100%;
}
